body{
    background: black;
}

.Header{
    padding: 14px;
}

.Logo{
    font-weight: bold;
    color: white;
    font-size: 30px;
    position: relative;
    display: inline-block;
    width: 140px;
    height: 36px;
}

.Logo > .Text{
    position: absolute;
    z-index: 2;
}

.Logo > .RedSquare{
    position: absolute;
    width: 30px;
    height: 30px;
    border: 1px solid palegoldenrod;
    background: hsl(0, 100%, 60%);
    z-index: 1;
    top: 0px;
    left: -6px;
}

.Logo > .BlueSquare{
    position: absolute;
    width: 30px;
    height: 30px;
    border: 1px solid palegoldenrod;
    background: hsl(240, 100%, 60%);
    z-index: 1;
    top: 0px;
    left: 68px;
}

.Game_Over{
    text-align: center;
    background: rgba(0,0,0,0.8);
    position: absolute;
    width: 312px;
    height: 416px;
    top: 0px;
    z-index: 2;
}

.Game_Over > .Game_Over_Text{
    color: red;
    font-weight: bold;
    font-size: 50px;
    width: 100%;
    top: 160px;
    position: absolute;
}

.Game_Board{
    background: rgb(19, 44, 82);
    display: inline-block;
    vertical-align: top;
    position: relative;
}

.Square_Box{
    border: 1px solid cyan;
    width: 50px;
    height: 50px;
    display: inline-block;
    vertical-align: top;
    position: relative;
}

.Square_Box-Filled{
    position: absolute;
    width: 30px;
    height: 30px;
    top: 8px;
    left: 8px;
    border: 2px solid palegoldenrod;
    z-index: 1;
}

.Square_Box_Link{
    position: absolute;
}

.Square_Box_Link.UP{
    width: 15px;
    height: 10px;
    top: 0px;
    left: 17.5px;
}

.Square_Box_Link.RIGHT{
    width: 10px;
    height: 15px;
    bottom: 17.5px;
    right: 0px;
}

.Square_Box_Link.DOWN{
    width: 15px;
    height: 10px;
    bottom: 0px;
    left: 17.5px;
}

.Square_Box_Link.LEFT{
    width: 10px;
    height: 15px;
    bottom: 17.5px;
    left: 0px;
}
.ControlPanel{
    text-align: center;
    margin-top: 20px;
}

.ControlPanel > div{
    vertical-align: top;
    display: inline-block;
}

.ControlPanel_Text{
    color: white;
}

.ControlPanel_key{
    color: #aaa;
    font: bold 9pt arial;
    text-decoration: none;
    text-align: center;
    margin: 5px;
    border: none;
    vertical-align: top;
    background: #eff0f2;
    border-radius: 4px;
    border-top: 1px solid #f5f5f5;
    background: #f8f8f8;
    box-shadow: inset 0 0 25px #e8e8e8,
    0 1px 0 #c3c3c3,
    0 2px 0 #c9c9c9,
    0 2px 3px #333;
    text-shadow: 0px 1px 0px #f5f5f5;
    -webkit-filter: dropshadow(color=#f5f5f5,offx=0, offy=1);
            filter: dropshadow(color=#f5f5f5,offx=0, offy=1); 
    outline: none;
    cursor: pointer;
}

.ControlPanel_key.SquareKey{
    width: 54px;
    height: 51px;
    font-size: 25px;
}

.ControlPanel_key.LongKey{
    width: 150px;
    height: 51px;
}

.ControlPanel_key:active{
    color: #888;
    background: #ebeced;
    -webkit-transform: translate(0, 3px);
            transform: translate(0, 3px);
    box-shadow: 
        inset 0 0 25px #ddd,
        0 0 3px #333;
    border-top: 1px solid #eee;
}
.Instruction{
    display: inline-block;
    vertical-align: top;
    width: calc(50% - 156px - 28px);
    height: calc(416px - 28px);
    color: white;
    text-align: center;
    padding: 14px;
}

.Instruction > h1{
    margin: 0px;
    padding: 5px;
    margin-bottom: 14px;
    color: yellow;
}

.Instruction > div + div{
    margin-top: 14px;
}

.Instruction_Img{
    width: 120px;
    display: inline-block;
    vertical-align: middle;
}

.Instruction_Img + div{
    display: inline-block;
    vertical-align: top;
    text-align: left;
    padding: 5px;
    width: calc(100% - 130px);
}
.Info_Board{
    display: inline-block;
    vertical-align: top;
    width: calc(50% - 156px - 28px);
    height: calc(416px - 28px);
    color: white;
    text-align: center;
    padding: 14px;
}

.Info_Section + .Info_Section{
    margin-top: 14px;
}

.Next_Title, .Stage_Title, .Score_Title{
    font-size: 32px;
    padding: 5px;
}

.Score_Number, .Stage_Number{
    font-size: 40px;
    padding: 5px;
    text-align: center;
    min-width: 50px;
    background: rgba(255,255,255,0.1);
    display: inline-block;
    height: 40px;
    font-weight: bold;
}

.Next_Box{
    border: 4px solid cyan;
    width: 100px;
    height: 100px;
    position: relative;
    background: rgb(19, 44, 82);
    margin: 0 auto;
}

.Next_Box-Filled{
    position: absolute;
    width: 60px;
    height: 60px;
    top: 16px;
    left: 16px;
    border: 4px solid palegoldenrod;
    z-index: 1;
}

.Next_Box_Link{
    position: absolute;
}

.Next_Box_Link.UP{
    width: 30px;
    height: 20px;
    top: 0px;
    left: 35px;
}

.Next_Box_Link.RIGHT{
    width: 20px;
    height: 30px;
    bottom: 35px;
    right: 0px;
}

.Next_Box_Link.DOWN{
    width: 30px;
    height: 20px;
    bottom: 0px;
    left: 35px;
}

.Next_Box_Link.LEFT{
    width: 20px;
    height: 30px;
    bottom: 35px;
    left: 0px;
}

.START_btn, .PAUSE_btn, .RESUME_btn, .RETRY_btn{
    color: white;
    padding: 5px;
    font-size: 32px;
    border: none;
    border-radius: 3px;
    outline: none;
    min-width: 140px;
}

.START_btn{
    background: hsl(0, 80%, 40%);
}

.PAUSE_btn{
    background: hsl(120, 80%, 40%);
}

.RESUME_btn{
    background: hsl(240, 80%, 40%);
}

.RETRY_btn{
    background: hsl(30, 80%, 40%);
}
